.app {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
}

.filters {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  align-items: center;
}

.select,
.price-filter input {
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.price-filter {
  display: flex;
  align-items: center;
  gap: 10px;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 20px;
}

.product-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 12px;
  text-align: center;
  background: #fff;
  box-shadow: 0px 2px 5px rgba(0,0,0,0.1);
  transition: transform 0.2s ease-in-out;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-card img {
  max-height: 150px;
  width: auto;
  margin: 0 auto 10px;
  display: block;
}

.product-card h2 {
  font-size: 16px;
  margin: 10px 0;
  font-weight: bold;
}

.product-card .category {
  color: #666;
  font-size: 14px;
}

.product-card .price {
  font-size: 18px;
  font-weight: bold;
  color: #1a73e8;
}
